.message {
    margin-top: 70px;
    border: 2px solid #dedede;
    background-color: rgba(5, 242, 64, 1);
    border-radius: 5px;
    max-width: 350px;
    padding-left: 20px;
    margin-left: 10px;
}

.angle {
    position: relative;
    width: 15px;
    height: 15px;
    background: radial-gradient(circle at 0 0, rgba(0,0,0,0) 15px, rgba(6, 97, 29, 1) 0);
    margin-left: 32px;
    top: 70px;

}

.message img {
    position: relative;
    float: left;
    max-width: 60px;
    width: 100%;
    margin-top: 20px;
    border-radius: 50%;
    right: 18px;
}

.time {
    float: right;
    color: #aaa;
}

.nickname {
    color: #5b05a1;
    text-align: left;
}

.message_item {
    color: white;

}

.message_window {
    position: relative;
    text-align: center;
    background-color: rgba(6, 97, 29, 1);
    max-width: 300px;
    max-height: 100px;
    border-radius: 5px;
    left: 45px;
    bottom: 5px;
}