.default {
    outline: none;
    margin: 5px;
    font-family: -apple-system, serif;
    border-radius: 10%;
    border: 1px solid black;
    background-color: #e7e7e7;
    color: black;
    -webkit-transition-duration: 0.4s;
    transition-duration: 0.4s;
    cursor: pointer;
}

.default:hover {
    background-color: white;
}

.default:disabled {
    background: rgba(161, 250, 194, 0.56);
    opacity: 0.6;
    cursor: not-allowed;
}
.default:disabled:hover {
    border: 1px solid white;
}

.button {

}

.red {
    cursor: pointer;
    font-family: -apple-system, serif;
    border-color: rgba(239, 40, 75, 0.82);
    color: rgba(255, 255, 255, 0.86);
    box-shadow: 0 0 40px 40px rgba(239, 40, 75, 0.82) inset, 0 0 0 0 rgba(239, 40, 75, 0.82);
    -webkit-transition: all 150ms ease-in-out;
    transition: all 150ms ease-in-out;
}

.red:hover {
    box-shadow: 0 0 10px 0 rgba(239, 40, 75, 0.82) inset, 0 0 10px 4px rgba(239, 40, 75, 0.82);
    color: #0a0808;
}